.modal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 320px;
  position: fixed;
  animation: fadeIn 0.3s linear;
  left: 50%;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
  background-color: @basic-white;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);

  &--hidden {
    display: none;
  }

  &--success {
    padding-top: 30px;
  }

  &--failure {
    padding-top: 40px;
    padding-bottom: 35px;
  }
}

.modal--failure button {
  width: 180px;
  margin-right: auto;
  margin-left: auto;
}

.modal__title {
  margin: 0;
  padding: 0;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
}

.modal__description {
  margin: 0;
  margin-top: 20px;
  margin-bottom: 22px;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  padding-left: 50px;
  padding-right: 50px;
}

.modal__button {
  border: none;
  width: 100%;
  padding-top: 18px;
  padding-bottom: 14px;
  padding-left: auto;
  padding-right: auto;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
}

.modal__close {
  padding: 20px;
  background-color: @special-extra-grey;
}

@media (min-width: @tablet-width) {
  .modal--success {
    width: 640px;
    padding-top: 38px;
  }

  .modal--failure {
    width: 400px;
    padding-top: 40px;
    padding-bottom: 35px;
    padding-left: 42px;
    padding-right: 42px;

    & button {
      width: 272px;
    }

    & h3 {
      padding-left: 80px;
      padding-right: 80px;
    }
  }

  .modal__title {
    font-size: 30px;
    line-height: 40px;
  }

  .modal__button {
    font-size: 21px;
  }

  .modal__close {
    padding-left: 91px;
    padding-right: 91px;
  }
}

@media (min-width: @desktop-width) {
  .modal--success {
    width: 640px;
    padding-top: 38px;
  }

  .modal--failure {
    width: 400px;
    padding-top: 40px;
    padding-bottom: 35px;
    padding-left: 42px;
    padding-right: 42px;
  }
}
